import { createRouter, createWebHistory } from 'vue-router'
//import HomeView from '../views/HomeView.vue'
import ClassView from '../views/ClassView.vue'
import RulesView from '@/views/RulesView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: ClassView
  },
  {
    path: '/rules',
    name: 'rules',
    component: RulesView
  },
  {
    path: '/druid',
    name: 'druid',
    component: ClassView
  },
  {
    path: '/warrior',
    name: 'warrior',
    component: ClassView
  },
  {
    path: '/rogue',
    name: 'rogue',
    component: ClassView
  },
  {
    path: '/ronin',
    name: 'ronin',
    component: ClassView
  },
  {
    path: '/shaman',
    name: 'shaman',
    component: ClassView
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
